<template>
  <div class="edit">
    <h2 style="text-align: center;">在线编辑</h2>
    <div class="edit__body">
      <div class="edit__input">
        <textarea class="edit__textarea" spellcheck="false" v-model="code" :onfocus="focus" :onblur="blur"></textarea>
      </div>
      <div class="edit__shower" id="edit-shower">
        <sr-article-container ref="render" height="100%" domId="edit-shower"></sr-article-container>
      </div>
    </div>
  </div>
</template>
  
<script>
export default {
  name: 'edit',
  data() {
    return {
      textareaHasChange: false,
      code: '',
      allowCtrlS: true,
    }
  },
  mounted() {
    window.scrollTo({ top: 0 })
    let that = this
    window.onbeforeunload = function (e) {
      let fp = that.$route.fullPath
      fp = fp.slice(0, fp.indexOf('#') === -1 ? fp.length : fp.indexOf('#'))
      if (fp === '/edit' && that.code !== '') {  // 注意这里要替换成自己的当前页面的路由
        e.returnValue = '关闭提示'
      } else {
        window.onbeforeunload = null
      }
    }
    document.addEventListener('keydown', this.keydown)
  },
  beforeUnmount() {
    document.removeEventListener('keydown', this.keydown)
  },
  beforeRouteLeave(to, from, next) {
    if (this.code !== '') {
      const answer = window.confirm('系统可能不会保存你所做的修改')
      if (answer) {
        next()
      } else {
        next(false)
      }
    } else next()
  },
  methods: {
    focus() {
      this.allowCtrlS = false
    },
    blur() {
      this.allowCtrlS = true
    },
    keydown(e) {
      if( !this.allowCtrlS && e.ctrlKey  === true && e.keyCode === 83 ){
        e.preventDefault()
        this.render()
        return false // 截取返回false就不会保存网页了
      }
    },
    render() {
      this.$refs.render.setArticle(this.code)
      this.$refs.render.render()
    }
  }
}
</script>

<style>
.edit__body {
  position: relative;
  width: 100%;
  height: calc(100vh - 80px);
}

.edit__input {
  display: inline-block;
  height: calc(100% - 10px);
  width: calc(40% - 10px);
  max-width: calc(700px - 10px);
  padding: 5px;
}

.edit__shower {
  position: absolute;
  top: 0;
  display: inline-block;
  height: 100%;
  width: calc(100% - 700px - 10px);
  min-width: calc(60% - 10px);
  overflow: auto;
}

.edit__textarea {
  font-size: 18px;
  padding: 5px;
  width: calc(100% - 10px);
  height: calc(100% - 10px);
  resize: none;
}
</style>